@use './parts/variables';
@import './parts/header', './parts/landing-page', './parts/footer', './parts/store', './parts/game-page', './parts/library', './parts/loading', './parts/cart', './parts/all-free-games', './parts/free-coming-soon-section', './parts/marquee';

@keyframes fade-in {
  from {
    transform: translateX(-20px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  font-family: 'Outfit', sans-serif !important;
}

img {
	max-width: 100%;
}

* {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
}

body {
  background-color: variables.$black;
  overflow-x: hidden;
}

::-webkit-scrollbar { 
  display: none; 
}

section {
  animation: fade-in .4s ease-in-out forwards;
  opacity: 0;
}